import { useContext } from "react";
import { ThemeContext } from "../../context";
import "./product.css"
import React from 'react'


const Product = ({title,img,link}) => {
    const theme = useContext(ThemeContext);
  const darkMode = theme.state.darkMode;
    return (
        <div className="p" style={{border: darkMode && "2px solid #535353", boxShadow: darkMode && "0 0 #0000, 0 0 #0000, 0 10px 28px 3px rgb(51 185 84), 0 4px 6px -2px rgb(51 185 84)"}}>
            <div className="p-browser" style={{backgroundColor: darkMode && "#333"}} >
                <div className="p-title">
                    {title}
                </div>
                <div className="p-circle" style={{backgroundColor: darkMode && "#535353"}}></div>
                <div className="p-circle" style={{backgroundColor: darkMode && "#535353"}}></div>
                <div className="p-circle" style={{backgroundColor: darkMode && "#535353"}}></div>
            </div>
            <a href={link} target="_blank" rel="noopener noreferrer">
                <img src={img} alt="My works" className="p-img" />
            </a>
        </div>
    )
}

export default Product
