import "./toggle.css"
import Sun from "../../img/sun.png"
import Moon from "../../img/moon.png"
import { useContext } from "react"
import { ThemeContext } from "../../context"
import React from 'react'
const Toggle = () => {
    const theme = useContext(ThemeContext)
    const handleClick = () => {
        theme.dispatch({ type: "TOGGLE" })
    }
    
    return (
        <div className="t" style={{ backgroundColor: theme.state.darkMode ? "black" : "white" }}>
            <img src={Sun} alt="default mode" className="t-icon sun" />
            <img src={Moon} alt="dark mode" className="t-icon moon" />
            <div className="t-button" onClick={handleClick} style={{left: theme.state.darkMode ? 0 : 25, transition: "all 0.6s cubic-bezier(0, 0.88, 0.64, 0.74)"}}></div>
        </div >
    )
}

export default Toggle
