import "./contact.css"
import Email from "../../img/email.png"
import Address from "../../img/address.png"
import { useContext, useRef, useState } from "react"
import emailjs from 'emailjs-com'
import { ThemeContext } from "../../context"
import React from 'react'
const Contact = () => {

    const formRef = useRef()
    // formref is for form sending 

        // setDone will will true  if user message is sussful sent to us and done function will be show 
        const [done, setDone] = useState(false)

        const theme = useContext(ThemeContext);
        const darkMode = theme.state.darkMode;

        // this function is from email.js  documation for react => https://www.emailjs.com/docs/examples/reactjs/
        const handelSubmit = (e) =>{
            e.preventDefault();
            emailjs.sendForm('service_zsv886q', 'template_c81mh82', formRef.current, 'user_wyRZF5UWzkbeAE5TPIgh0')
            .then((result) => {
                console.log(result.text);
                setDone(true)
                e.target.reset()
                // document.getElementById("contact-form").reset();
                //     return;
            }, (error) => {
                console.log(error.text);
            });

        }

    return (
        <div className="c" style={{backgroundColor: darkMode && "#333"}}>
            <div className="c-bg"></div>
            <div className="c-wrapper">
                <div className="c-left">
                    <h1 className="c-title">Let's discuss your project</h1>
                    <div className="c-info">
                        <div className="c-info-item">
                            <img src={Email} alt="contact me" className="c-icon" />
                            <p className="c-link c-email" style={{color: darkMode && "fff"}}>
                                <a href="mailto:contact@tejbahadurkarki.name.np" style={{color: darkMode && "white"}}>
                                    contact@tejbahadurkarki.name.np
                                </a>
                            </p>
                        </div>
                        <div className="c-info-item">
                            <img src={Address} alt="Address" className="c-icon" />
                            <p className="c-link">
                                <a href="https://goo.gl/maps/5o4WpmYFMfbvM49H7" style={{color: darkMode && "white"}}>
                                Itahari, Sunsari, NEPAL
                                </a>
                            </p> 
                        </div>
                    </div>
                </div>
                <div className="c-right">
                    <p className="c-desc">
                        <b>What's your story? </b> Get in touch. Always available for freelancing if the right project comes along me.
                    </p>
                    
                    <form ref={formRef} onSubmit={handelSubmit} className="contact-form">
                        <input style={{backgroundColor: darkMode && "#333", color: darkMode && "white" }}type="text" autoFocus placeholder="Name *" name="user_name" autoComplete="off" minLength="3" required />
                        <input style={{backgroundColor: darkMode && "#333", color: darkMode && "white"}} type="text" placeholder="Subject *" name="user_subject" autocomplete="off" required />
                        <input style={{backgroundColor: darkMode && "#333", color: darkMode && "white"}} type="email" inputMode="email" aria-describedby="emailHelp" placeholder="Email *" name="user_email" required />
                        <input style={{backgroundColor: darkMode && "#333", color: darkMode && "white"}} type="text" inputMode="tel" placeholder="Number" name="user_number" autocomplete="off" minlength="10" maxlength="14"/> 
                        <textarea style={{backgroundColor: darkMode && "#333", color: darkMode && "white"}} name="message" id="user_message" placeholder="Message *" rows="5" required minLength="10" maxlength="200"></textarea>
                        <button className="button"><span>Submit</span></button>

                        {/* after successfully message sent to us it will shop message to sender */}
                        {/* {done && "thank you"} */}

                        {/* giving upper function animation wilth css  */}
                        {done &&  <span className="c-message" aria-labelledby="message" role="img">Your message has been sent successfully. I will 📨 ☎️Contact you soon. <br /><b>THANK YOU.... </b>😊</span>}
                    </form>
                </div>
                
            </div>
        </div>
    )
}

export default Contact
