import About from "./components/about/About";
import Footer from "./components/footer/Footer";
import ProductList from "./components/productList/ProductList";
import Intro from "./components/intro/Intro";
import Contact from "./components/contact/Contact";
import Toggle from "./components/toggle/Toggle";
import { useContext } from "react";
import { ThemeContext } from "./context";
import { Route, Routes, Link } from "react-router-dom";
// import Copyright from "./components/copyright/Copyright";
import Nav from "./components/navination/Nav";
import './app.css';
import { Error, Home } from "@material-ui/icons";
import React from 'react'
const App = () => {
  const theme = useContext(ThemeContext);
  const darkMode = theme.state.darkMode;
  return <div style={{ backgroundColor: darkMode ? "#272726" : "white", color: darkMode && "#e5e5e5" }}>
    <Toggle />
    <Routes>
      <Route exact path="/" element={[<Intro />, <About />, <ProductList />, <Footer />]} />
      <Route path='about' element={<About />} />
      <Route path='projects' element={<ProductList />} />
      <Route path='contact' element={<Contact />} />
      <Route path="*" element={<Page404 />} />
    </Routes>
    {/* <Copyright /> */}
    <Nav />
  </div>;
};

export default App

function Page404() {
  const theme = useContext(ThemeContext);
  const darkMode = theme.state.darkMode;

  return (
    <section className="mLost" style={{ backgroundColor: darkMode ? "#e5e5e5" : "white", color: darkMode && "#e5e5e5" }}>
      <Toggle style={{ display: "none" }} />

      <div className="lost">
        <div className="heading">
            <Error style={{color: "#59B256", justifyContent: "center", width: "100%"}}/>
          <p clssName="hedding" >
            Are you Lost <br/> Let's get back to
          </p>
        </div>
        <div className="link">
          <Link to="/" style={{ color: "white", backgroundColor: "pink"}}><Home />HOME</Link>
        </div>
      </div>
    </section>
  );
}